/*Profile Styles*/
.profile {
  display:-webkit-flex;
  display:flex;
  -webkit-justify-content: center;
          justify-content: center;   
}

.profile-info {
  margin-left: 10px;
  text-align: center;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
}

.profile-img {
  width: 150px;
  height: 150px;
  margin-top: 20px;
  margin-left: 600px;
  -webkit-justify-content: center;
          justify-content: center;
  display: -webkit-flex;
  display: flex;
  border: 3px solid #fff;
  border-radius: 75px;
  object-fit: cover;
}

.profile-name {
  font-size: 32px;
}

.profile-followers, .profile-genres {
  font-size: 24px;
}

/* Gallery Styles */
.tracks-display {
  margin-top: 20px;
  -webkit-justify-content: center;
          justify-content: center;
  justify-items: center;
  justify-self: center;
  margin-left: 60px;    
}

.track {
display: inline-block;
width: 220px;
height: 220px;
margin: 10px;
text-align: left;
cursor: pointer;
position: relative;
}

.track-img {
width: 220px;
height: 220px;
border: 3px solid #fff;
border-radius: 3px;
object-fit: cover;
position: absolute;
}

.track-text {
background-color: #000;
color: #fff;
font-size: 14px;
opacity: 0.75;
width: 214px;
margin-left: 3px;
text-align: center;
position: absolute;
bottom: 0;
padding: 5px;
}

.track-play {
  position: absolute;
  width: 220px;
  height: 220px;
}

.track-play-inner {
  position: absolute;
  text-align: center;
  font-size: 25px;
  background-color: #000;
  border-radius: 30px;
  width: 60px;
  height: 60px;
  color: #fff;
  padding-top: 13px;
  margin-left: 80px;
  margin-top: 80px;
  opacity: 0;
}

.track-play-inner:hover {
  opacity: 0.8;
  -webkit-animation-name: fade-in;
          animation-name: fade-in;
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
}

.auth form {
  margin-top: 1em;
  max-width: 50%;
}

.buttons {
  margin-left: 1em;
}
